'use client';
import { createPost, fetchOEmbed } from '@/features/videos/video.server';
import { YoutubeOEmbedResponse } from '@/features/videos/video.type';
import { useEffect, useState } from 'react';

export default function PostsPage() {
  const [url, setUrl] = useState('');
  const [text, setText] = useState('');
  const [video, setVideo] = useState<YoutubeOEmbedResponse | null>(null);
  const [message, setMessage] = useState<string | null>(null);

  // URL入力が変わる毎にフェッチ（500msでデバウンス）
  useEffect(() => {
    if (!url) return;

    const timer = setTimeout(async () => {
      try {
        // 動画のプレビューを取得
        const video = await fetchOEmbed(url);
        setVideo(video);
        setMessage(null);
      } catch (e) {
        if (e instanceof Error) {
          setMessage(e.message);
          setVideo(null);
        } else {
          setMessage('エラーが発生しました');
        }
      }
    }, 500);

    // クリーンアップ
    return () => {
      clearTimeout(timer);
    };
  }, [url]);

  const handleSubmit = async (formData: FormData) => {
    try {
      await createPost(formData);
      setMessage('投稿しました！'); //後でリダイレクトに変更
    } catch (e) {
      if (e instanceof Error) {
        setMessage(e.message);
      } else {
        setMessage('エラーが発生しました');
      }
    }
  };

  return (
    <div className="max-w-7xl mx-auto shadow-sm rounded-xl p-10">
      <form action={handleSubmit} className="space-y-6">
        {/* URL */}
        <div>
          <label className="block text-sm font-medium mb-2">YouTube URL</label>
          <input
            name="url"
            value={url}
            onChange={(e) => setUrl(e.target.value)}
            placeholder="https://www.youtube.com/watch?v=..."
            className="w-full rounded-lg border border-gray-500 p-1.5 text-sm"
          />
        </div>

        {/* プレビュー */}
        <div className="rounded-xl border bg-gray-100 p-4">
          {video ? (
            <div className="flex gap-4">
              <img
                src={video.thumbnail_url}
                alt=""
                className="w-40 aspect-video rounded-md object-cover"
              />
              <div className="flex flex-col justify-center">
                <p className="font-medium leading-snug">{video.title}</p>
                <p className="text-sm text-gray-600 mt-1">{video.author_name}</p>
              </div>
            </div>
          ) : (
            <p className="text-sm text-gray-400">
              URLを入力すると、ここに動画のプレビューが表示されます
            </p>
          )}
        </div>

        {/* テキスト */}
        <div>
          <label className="block text-sm font-medium mb-1">学習メモ</label>
          <textarea
            name="text"
            value={text}
            onChange={(e) => setText(e.target.value)}
            placeholder="動画を見て学んだことを自由に書いてください"
            className="w-full rounded-lg border px-3 py-2 text-sm min-h-[200px] resize-y focus:outline-none focus:ring"
          />
        </div>

        {/* ボタン */}
        <button
          type="submit"
          className="rounded-lg bg-black text-white px-6 py-2 text-sm hover:opacity-90"
        >
          投稿する
        </button>

        {message && <p className="text-sm text-center text-gray-600">{message}</p>}
      </form>
    </div>
  );
}
